@tailwind base;
@tailwind components;
@tailwind utilities;

/* Root  */

:root {
  /* https://coolors.co/d3fac7-613a3a-035e7b-f6ae2d-ffb7c3 */
  /* Colour Palette 1 */
  --lime: #d3fac7;
  --brown: #613a3a;
  --blue: #035e7b;
  --yellow: #f6ae2d;
  --pink: #ffb7c3;
  --black: #110A0B;
  --colour1: #D8E2DC;
  --colour2: #FFE5D9;
  --colour3: #FFCAD4;
  --colour4: #F4ACB7;
  --colour5: #9D8189;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  max-width: 100vw;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Doc  */

h3 {
  color: var(--black);
}

