.title-shapes {
  position:absolute;
  top: 60vh;
  left: 45vw;
  filter: blur(0.5px);
}

.title-shapes .triangle {
  position:absolute;
  width: 0; 
  height: 0; 
  border-left: 30px solid transparent;
  border-right: 30px solid transparent;
  border-bottom: 55px solid rgb(255, 0, 0);
  opacity: 0.3;
}

.title-shapes .square {
  position:absolute;
  width: 55px;
  height: 55px;
  left: 3vw;
  background-color: rgb(0, 255, 40);
  opacity: 0.3;
}

.title-shapes .circle {
  position:absolute;
  width: 60px;
  height: 60px;
  border-radius: 30px;
  left: 6vw;
  background-color: rgb(0, 5, 255);
  opacity: 0.3;
}

