/* Who I am */

.who-section {
  width: 50vw;
}

.who-card {
  background-color: var(--brown);
  height: 450px;
  margin-top: 2vh;
  margin-left: 2vh;
  margin-bottom: 2vh;
  margin-right: 2vh;
}

.who-card.left {
  transform: translate(-10px, 0);
}

.who-card.right {
  transform: translate(10px, 0);
}

.who-card img {
	max-height: 300px;
}